@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $green2;
    height: 68px;
    padding: 0 44px;
    position: relative;
    &:before{
        content: '';
        position: absolute;
        top: 0;
        width: 20px;
        height: 100%;
        background-color: inherit;
        transition: all .3s;
    }
    &:hover{
        background-color: $primaryHover;
        color: $green2;
        transform: scaleX(1.02);
    }
    &:active{
        color: $green2;
        background-color: $primary;
        transform: unset !important;
        &:before{
            opacity: 0;
            width: 0;
        }
    }
}

.m-btn-triangle-right{
    padding-right: 40px;
    &:before{
        left: 100%;
        clip-path: polygon(0% 0,0 100%,100% 50%);
        transform: translateX(-1px);
    }
}

.m-btn-triangle-left{
    padding-left: 40px;
    transform: translateX(10px);
    &:before{
        right: 100%;
        clip-path: polygon(100% 0,0 50%,100% 100%);
        transform: translateX(1px);
    }
}

.m-btn-small{
    height: 40px;
    padding: 0 20px;
    &:before{
        width: 16px;
    }
    &.m-btn-triangle-right{
        padding-right: 16px;
        transform: translateX(-8px);
    }
    &:hover{
        transform: scale(1.03) !important;
    }
}



@media screen and (max-width: 600px){
    .m-btn-primary{
        padding: 0 28px;
        height: 55px;
        &:before{
            width: 15px;
        }
    }

    .m-btn-triangle-right{
        padding-right: 24px;
        transform: translateX(-7.5px);
    }

    .m-btn-small{
        height: 39px;
        padding: 0 20px;
    }
}
