.modal-backdrop {
    &.show {
        opacity: 0.7;
    }
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    @media screen and (min-width: 601px) {
        text-align: center;
        &:before {
            display: inline-block;
            vertical-align: middle;
            content: " ";
            height: 100%;
        }
        .modal__dialog {
            display: inline-block;
            text-align: left;
            vertical-align: middle;
        }
    }
    &--active {
        opacity: 1;
        pointer-events: unset;
    }
    &__bg{
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        background: rgba(#000, 0.6);
    }
    &__dialog {
        margin: auto;
        background-color: #fff;
        width: 600px;
        max-width: calc(100% - 20px);
        padding: 50px 30px 40px;
        position: relative;
    }
    &__close{
        font-size: 1em;
        position: absolute;
        top: 20px;
        right: 20px;
        width: 20px;
        height: 20px;
        svg{
            width: 100%;
            height: auto;
            transition: all .3s;
        }
        &:hover{
            svg{
                opacity: .6;
            }
        }
    }
    .news-card__date{
        margin: 0;
    }
}


