.header{
  z-index: 5;
  padding: 36px 0;
  background-color: #fff;
  &__btn-mob{
    display: none;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 265px;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__contacts{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__action{
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: 28px;
    transform: unset !important;
  }
  &__login{
    display: flex;
    align-items: center;
    color: $green;
    svg{
      margin-right: 8px;
    }
    &:hover{
      color: $primaryHover;
      path{
        stroke: $primaryHover;
      }
    }
    &:active{
      color: #FFFFFF;
      path{
        stroke: #FFFFFF;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      width: 225px;
    }
  }
}


@media screen and (max-width: 768px){
  .header{
    padding: 0;
    &._fix{
      z-index: 1000;
      position: sticky;
      top: 0;
      width: 100%;
      transform: translateY(-59px);
    }
    &__inner{
      padding: 17px 0;
    }
    &__btn.m-btn{
      display: none;
    }
    &__btn-mob{
      width: 100%;
      height: 48px;
      display: flex;
      background: $red;
      color: #fff;
      font-weight: 700;
      font-size: 12px;
      line-height: 15px;
      position: sticky;
      top: 0;
      &:hover{
        background: darken($red,10%);
        color: #fff;
      }
    }
  }
}


@media screen and (max-width: 600px){
  .header{

    &__logo{
      width: 134px;
    }
    &__login{
      svg{
        width: 25px;
        height: auto;
        margin-right: 0;
      }
      span{
        display: none;
      }
    }
    &__btn{
      transform: translate(36%,-90%) rotate(-90deg) !important;
    }
  }
}
