@import "../mixins";
.display1{
  font-weight: 900;
  font-size: 44px;
  line-height: 136%;
  text-transform: uppercase;
}

.display2{
  font-weight: 800;
  font-size: 40px;
  line-height: 120%;
}

.display3{
  font-weight: 800;
  font-size: 36px;
  line-height: 100%;
}

.display4{
  font-weight: 800;
  font-size: 28px;
  line-height: 129%;
}

.display5{
  font-weight: 700;
  font-size: 22px;
  line-height: 127%;
}

.text20{
  font-size: 20px;
  line-height: 160%;
}

.text16{
  font-size: 16px;
  line-height: 150%;
}

.text14{
  font-size: 14px;
  line-height: 143%;
}

.text13{
  font-size: 13px;
  line-height: 123%;
}

.text12{
  font-size: 12px;
  line-height: 125%;
}

.medium-weight{
  font-weight: 500;
}

.semi-bold-weight{
  font-weight: 600;
}

.bold-weight{
  font-weight: 700;
}

.extra-bold-weight{
  font-weight: 800;
}

.gray-text{
  color: $gray;
}

.gray-middle-text{
  color: $grayMiddle;
}

.green-text{
  color: $green;
}

.yellow-text{
  color: $primary;
}

.custom-line{
  position: relative;
  color: $green;
  &:before{
    content: '';
    position: absolute;
    width: calc(100% + 10px);
    height: 16px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    opacity: 0.2;
    background-color: $primary;
    z-index: -1;
  }
}

.m-ul{
  @include grid100gap(8px);
  li{
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: $grayMiddle;
    display: flex;
    align-items: center;
    &:before{
      content: '';
      width: 12px;
      height: 100%;
      flex-shrink: 0;
      background-image: url(../img/round.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% auto;
      margin-right: 8px;
    }
  }
}


@media screen and (max-width: 600px){
  .display1{
    font-size: 24px;
    line-height: 150%;
  }

  .display2{
    font-size: 22px;
    line-height: 127%;
  }

  .display3{
    font-size: 20px;
    line-height: 130%;
  }

  .display4{
    font-size: 18px;
    line-height: 139%;
  }

  .display5{
    font-size: 16px;
    line-height: 150%;
  }

  .text20{
    font-size: 14px;
    line-height: 157%;
  }

  .text16,
  .text14{
    font-size: 12px;
    line-height: 150%;
  }

  .text13{
    font-size: 12px;
    line-height: 125%;
  }

  .text12{
    font-size: 8px;
    line-height: 125%;
  }

  .mob-text10{
    font-size: 10px;
  }

  .mob-text8{
    font-size: 8px;
  }

  .m-ul{
    li{
      font-size: 12px;
      line-height: 18px;
    }
  }

  .custom-line{
    &:before{
      height: 8px;
      width: calc(100% + 8px);
    }
  }
}
