@import "../mixins";
.inspect-card{
  @media screen and (min-width: 601px){
    &:last-child{
      .inspect-card__line{
        display: none;
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: 12px;
  }
  &__num{
    font-weight: 800;
    font-size: 32px;
    line-height: 112%;
    color: $primary;
    flex-shrink: 0;
  }
  &__info{
    margin-left: 4px;
    flex-shrink: 0;
    position: relative;
    &._active{
      .inspect-card__modal{
        opacity: 1;
        pointer-events: unset;
      }
      .inspect-card__question{
        color: #FFFFFF;
        background-color: $green;
      }
    }
  }
  &__modal{
    position: absolute;
    left: 0;
    top: 100%;
    margin-top: 9px;
    width: 440px;
    max-width: calc(100vw - 50px);
    padding: 24px 28px;
    background-color: $green;
    color: #fff;
    z-index: 5;
    opacity: 0;
    pointer-events: none;
    &-close{
      position: absolute;
      top: 12px;
      right: 12px;
      img{
        width: 10px;
      }
    }
  }
  &__question{
    width: 20px;
    height: 20px;
    border: 1px solid $green;
    border-radius: 50%;
    @include center;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: #FFFFFF;
      background-color: $green;
    }
  }
  &__line{
    position: relative;
    width: 100%;
    margin-left: 8px;
    padding-left: 10px;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      border-top: 2px dashed #757482;
      opacity: .3;
      width: 100%;
    }
    img{
      width: 12px;
    }
  }
}

.advantage-card{
  padding: 36px;
  background-color: $graySuperLight;
  position: relative;
  &:nth-child(1){
    .advantage-card__img{
      right: 15px;
      bottom: 8px;
    }
  }
  &:before{
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    width: 125px;
    height: 185px;
    clip-path: polygon(100% 0,0 100%,100% 100%);
    background-color: $primary;
    pointer-events: none;
  }
  &__list{
    margin-top: 24px;
    max-width: 227px;
    position: relative;
    z-index: 2;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 100%;
    max-height: 100%;
  }
}

.tariff-card{
  padding: 36px;
  background-color: $graySuperLight;
  display: flex;
  flex-direction: column;
  &__items{
    margin: 24px 0;
    @include grid100gap(12px);
  }
  &__item{
    display: flex;
    align-items: center;
    position: relative;
    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    color: $gray;
    &:before{
      content: '';
      width: 24px;
      min-height: 24px;
      height: 100%;
      flex-shrink: 0;
      background-image: url(../img/check.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% auto;
      margin-right: 8px;
    }
    &._disabled{
      color: $grayLight;
      &:before{
        background-image: url(../img/check2.svg);
      }
    }
    &._rouble{
      &:before{
        background-image: url(../img/rouble.svg);
      }
    }
  }
  &__btn{
    margin-top: auto;
    width: calc(100% - 10px);
  }
}

.step-card{
  padding: 36px;
  background-color: $green;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  &:hover{
    .step-card__content{
      opacity: 0;
    }
    .step-card__content2{
      opacity: 1;
    }
  }
  &:last-child{
    .step-card__line{
      display: none;
    }
  }
  &__content{
    transition: all .3s;
  }
  &__content2{
    transition: all .3s;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    @include center;
    padding: 8px;
  }
  &__img{
    height: 56px;
    width: auto;
    margin-bottom: 24px;
    img{
      height: 100%;
    }
  }
  &__img2{
    max-width: 100%;
    max-height: 100%;
  }
  &__zoom,
  &__round-wrap{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  &__round{
    padding-bottom: 100%;
    width: 100%;
    background: #FFFFFF;
    opacity: 0.1;
    border-radius: 50%;
    display: block;
    &-wrap{
      width: calc(100% - 36px);
    }
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__title{
    flex-shrink: 0;
  }
  &__desc{
    color: #fff;
    margin-top: auto;
    padding-top: 60px;
  }
  &__line{
    position: relative;
    width: 100%;
    margin-left: 8px;
    padding-left: 14px;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      border-top: 2px dashed #fff;
      opacity: .3;
      width: calc(100% + 36px);
    }
    img{
      width: 12px;
    }
  }
}

.qa-card{
  padding: 36px 36px 36px 45px;
  width: calc(100% - 21px);
  margin-left: auto;
  cursor: pointer;
  position: relative;
  &._active{
    background-color: $graySuperLight;
    .qa-card__btn{
      transform: rotate(180deg) translate(50%,50%);
    }
  }
  &__btn{
    background-image: url(../img/btn-rect.svg);
    background-size: 100% auto;
    background-repeat: no-repeat;
    width: 42px;
    height: 44px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-50%,-50%);
    @include center;
    transition: all .4s;
    &:hover{
      transform:translate(-50%,-50%) scale(1.05);
    }
  }
  &__hidden{
    padding-top: 12px;
    display: none;
  }
}

.news-card{
  &:hover{
    cursor: pointer;
    .news-card__img{
      img{
        transform: scale(1.25);
      }
    }
  }
  &__img{
    position: relative;
    padding-bottom: 58.7%;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
      transition: all 1s;
    }
  }
  &__content{
    margin-top: 36px;
  }
  &__title{
    margin-top: 8px;
  }
  &__desc{
    margin-top: 10px;
  }
}


@media screen and (max-width: 1200px){
  .advantage-card{
    &__title,
    &__list{
      max-width: 546px;
    }
  }
}


@media screen and (max-width: 992px){
  @media screen and (min-width: 600px){
    .inspect-card{
      &__modal{
        width: 350px;
      }
      &:nth-child(5n - 1),
      &:nth-child(5n){
        .inspect-card__modal{
          left: unset;
          right: 0;
        }
      }
    }
  }

  .tariff-card{
    padding: 28px;
  }

  .news-card{
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 16px;
    &__content{
      margin: 0;
      padding-left: 20px;
    }
  }
}


@media screen and (max-width: 600px){
  .inspect-card{
    display: flex;
    align-items: center;
    padding-left: 20px;
    position: relative;
    &:last-child{
      .inspect-card__line{
        &:before{
          height: 11px;
        }
      }
    }
    &__top{
      margin: 0 8px 0 0;
    }
    &__line{
      position: absolute;
      height: 100%;
      width: 12px;
      top: 0;
      left: 0;
      transform: translateX(-50%);
      margin: 0;
      padding-left: 0;
      &:before{
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 2px;
        height: 100%;
        border-top: none;
        border-left: 2px dashed $grayMiddle;
      }
      img{
        position: absolute;
        top: 11px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    &__info{
      margin-left: 8px;
    }
    &__num{
      font-variant-numeric: tabular-nums;
      -webkit-font-feature-settings: "tnum";
      font-feature-settings: "tnum";
    }
    &__info{
      position: static;
    }
    &__modal{
      top: 100%;
      left: -20px;
      width: calc(100% + 40px);
      max-width: unset;
      padding: 16px 20px;
      &-close{
        top: 8px;
        right: 8px;
      }
    }
  }

  .advantage-card{
    padding: 20px;
    &:nth-child(1){
      .advantage-card__img{
        right: 8px;
        bottom: 7.7px;
        max-width: 104px;
      }
    }
    &__title{
      font-size: 20px;
      line-height: 140%;
    }
    &__list{
      max-width: 200px;
      margin-top: 8px;
    }
    &__img{
      max-width: 164px;
      max-height: 126px;
    }
  }

  .tariff-card{
    width: 194px;
    flex-shrink: 0;
    padding: 20px;
    &:not(:last-child){
      margin-right: 8px;
    }
    &__items{
      margin: 12px 0;
      gap: 8px;
    }
    &__item{
      font-size: 12px;
      line-height: 18px;
    }
  }

  .step-card{
    padding: 20px;
    min-height: 240px;
    &__img{
      height: 44px;
    }
    &__desc{
      padding-top: 16px;
    }
  }

  .qa-card{
    padding: 20px 20px 20px 45px;
    &__hidden{
      padding-top: 8px;
    }
  }

  .news-card{
    grid-template-columns: 100%;
    gap: 20px;
    &__content{
      padding: 0;
    }
    &__title{
      max-width: 242px;
    }
  }
}
