@import "../mixins";
.footer{
  padding: 48px 0 104px;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__col{
    max-width: 372px;
  }
  &__title{
    margin-bottom: 24px;
  }
  &__links{
    @include grid100gap(12px);
    max-width: 273px;
  }
  &__group-title,
  &__link{
    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    color: $grayMiddle;
  }
  &__groups{
    @include grid100gap(16px);
  }
  &__apps{
    margin: 0 10px;
  }
  &__apps2{
    display: none;
  }
  &__app{
    padding: 13px 10px 13px 24px;
    display: grid;
    grid-template-columns: 50% 50%;
    align-items: center;
    max-width: 300px;
    &:not(:last-child){
      margin-bottom: 12px;
    }
    &-qr{
      img{
        width: 100%;
      }
    }
    &-img{
      display: block;
      max-width: 48px;
      max-height: 54px;
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner,
    &__apps2{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 30px;
    }
    &__apps{
      display: none;
    }
    &__apps2{
      grid-column: 1/-1;
    }
    &__market,
    &__telegram{
      width: calc(100% - 14px);
      max-width: 270px;
    }
    &__market{
      background-color: $grayDark;
      img{
        max-height: 40px;
        max-width: 100%;
      }
      &:hover,
      &:active{
        background-color: $grayDark;
      }
    }
    &__telegram{
      background: #61A8DE;
      color: #fff;
      img{
        width: 30px;
        flex-shrink: 0;
        margin-right: 10px;
      }
      &:hover,
      &:active{
        background-color:#61A8DE;
        color: #fff;
      }
      .text13{
        max-width: 100px;
      }
    }
  }
}


@media screen and (max-width: 600px){
  .footer{
    padding: 28px 0;
    &__inner{
      gap: 36px;
      grid-template-columns: 100%;
    }
    &__title{
      margin-bottom: 12px;
    }
    &__apps2{
      gap: 20px;
    }
    &__market,
    &__telegram{
      padding: 0 6px;
      height: 64px;
    }
    &__groups{
      gap: 12px;
    }
    &__links{
      max-width: 100%;
      column-count: 2;
      column-gap: 12px;
      display: block;
    }
    &__link{
      display: inline-block;
      width: 100%;
      &:not(:last-child){
        margin-bottom: 16px;
      }
    }
    &__group-title,
    &__link{
      font-size: 12px;
      line-height: 18px;
    }
  }
}
