@import "../mixins";
.main{
  background-size: cover;
  background-position: center;
  position: relative;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:  linear-gradient(99.09deg, #494629 0%, #44803F 100%);
    mix-blend-mode: normal;
    opacity: 0.75;
  }
  .container{
    position: relative;
    z-index: 3;
  }
  &__inner{
    padding: 14.37% 0 18.5%;
    color: #FFFFFF;
  }
  &__round{
    position: absolute;
    opacity: 0.2;
    filter: blur(100px);
    border-radius: 50%;
    z-index: 2;
  }
  &__round1{
    width: 501px;
    height: 501px;
    background-color: $primary;
    top: -233px;
    left: 252px;
  }
  &__round2{
    width: 589px;
    height: 589px;
    background-color: $red;
    right: -225px;
    bottom: -215px;
  }
  &__title{
    max-width: 1050px;
    margin: 0 auto;
    text-align: center;
  }
  &__info{
    display: flex;
    justify-content: center;
    margin-top: 32px;
  }
  &__group{
    &:not(:last-child){
      margin-right: 44px;
    }
    &-num{
      color: $primary;
      font-weight: 800;
      font-size: 48px;
    }
  }
  &__only{
    padding: 10px 34px 10px 20px;
    background: $green;
    max-width: 335px;
    clip-path: polygon(0 0,95.8% 0,100% 100%,0 100%);
  }
  &__nav{
    margin-top: 32px;
    display: flex;
    justify-content: center;
  }
}

.inspect{
  margin-top: -36px;
  //overflow: hidden;
  &__inner{
    background-color: $graySuperLight;
    padding: 36px 301px 36px 36px;
    position: relative;
    z-index: 3;
  }
  &__title,
  &__desc{
    position: relative;
    z-index: 2;
  }
  &__title{
    max-width: 340px;
  }
  &__desc{
    margin-top: 24px;
    max-width: 246px;
  }
  &__items{
    margin-top: 24px;
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 16px;
  }
  &__media{
    position: absolute;
    width: 582px;
    height: 100%;
    right: 0;
    top: 0;
    pointer-events: none;
  }
  &__triangle{
    width: 100%;
    &-mob{
      display: none;
    }
  }
  &__img{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(37px,-50%);
    width: 315px;
  }
}

.advantages{
  margin-top: 28px;
}

.advantages,
.tariffs{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc((100% / 3) - (56px / 3)));
    gap: 28px;
  }
}

.review{
  margin-top: 48px;
  padding: 48px 0 65px;
  &__inner{
    padding: 56px 20px 238px;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  &__info{
    @include grid100gap(12px);
    width: 400px;
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
    color: #fff;
  }
  &__pdf{
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%,50%);
    &:hover{
      transform: scaleX(1.02) translate(-50%,50%);
    }
    &:active{
      transform: scaleX(1) translate(-50%,50%) !important;
    }
    span{
      white-space: nowrap;
    }
    img{
      position: absolute;
      top: -27px;
      left: -18px;
    }
  }
}

.video-btn{
  width: 160px;
  height: 160px;
  @include center;
  position: relative;
  margin: 62px auto 0;
  &__rounds{
    transition: all .3s;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  &__round{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: $primary;
    border-radius: 50%;
  }
  &__round1{
    opacity: 0.2;
    width: 100%;
    height: 100%;
  }
  &__round2{
    opacity: 0.5;
    width: 82.5%;
    height: 82.5%;
  }
  &__round3{
    width: 62.5%;
    height: 62.5%;
  }
  img{
    position: relative;
    z-index: 4;
    transform: translateX(10.5%);
  }
  &:hover:not(:active){
    .video-btn__rounds{
      transform: scale(1.275) translate(-38%,-40%);
    }
  }
}

.help{
  padding: 65px 0 48px;
  display: grid;
  grid-template-columns: 50% 50%;
  color: #fff;
  &__col{
    padding: 64px;
    display: flex;
    flex-direction: column;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    &:nth-child(odd){
      .help__top{
        margin-left: auto;
        text-align: right;
      }
      .help__btn,
      .help__video{
        margin-left: auto;
        margin-right: 0;
      }
      .help__video{
        flex-direction: row-reverse;
        text-align: right;
        img{
          margin: 0 0 0 8px;
          transform: rotate(-180deg);
        }
      }
      .help__decor{
        top: unset;
        right: unset;
        bottom: 0;
        left: 0;
      }
    }
  }
  &__top{
    width: 521px;
    max-width: 100%;
    @include grid100gap(16px);
  }
  &__bottom{
    padding-top: 204px;
    margin-top: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__btn{
    margin-right: auto;
    width: 234px;
    transform: unset;
  }
  &__video{
    margin: 20px auto 0 0;
    display: flex;
    align-items: center;
    max-width: 310px;
    img{
      flex-shrink: 0;
      margin-right: 8px;
      width: 20px;
    }
  }
  &__decor{
    position: absolute;
    right: 0;
    top: 0;
    max-width: 100%;
    max-height: 100%;
    pointer-events: none;
  }
}

.develop{
  padding: 48px 0 55px;
  &__inner{
    position: relative;
    display: grid;
    grid-template-columns: 50% 50%;
    background-color: $graySuperLight;
  }
  &__left{
    padding: 162px 56px;
    background-color: $green;
    width: calc(100% - 65px);
    position: relative;
    color: #fff;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      width: 65px;
      height: 100%;
      background-color: inherit;
      transition: all .3s;
      clip-path: polygon(0% 0,0 100%,100% 50%);
      transform: translateX(-1px);
      left: 100%;
    }
  }
  &__badge{
    background: $primary;
  }
  &__title{
    margin-top: 12px;
    max-width: 300px;
  }
  &__desc{
    margin-top: 20px;
    max-width: 350px;
  }
  &__right{
    @include center;
    padding: 56px;
  }
  &__img{
    display: block;
    width: 420px;
    max-width: 100%;
  }
  &__decor{
    position: absolute;
    right: 0;
    top: 0;
    max-width: 100%;
    &-mob{
      display: none;
    }
  }
}

.tariffs{
  padding: 55px 0 48px;
  &__top{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__title{
    text-align: center;
  }
  &__badge{
    margin-top: 16px;
    background-color: $green;
    color: #fff;
  }
  &__grid{
    margin-top: 40px;
  }
}

.how-work{
  padding: 48px 0;
  &__title{
    text-align: center;
  }
  &__grid{
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - (84px / 4)));
    gap: 28px;
  }
}

.service{
  padding: 48px 0;
  overflow: hidden;
  &__inner{
    display: flex;
  }
  &__left{
    width: 472px;
    margin-right: 28px;
    flex-shrink: 0;
    position: relative;
    z-index: 3;
  }
  &__right{
    width: calc(100% - 500px);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      right: 100%;
      top: 0;
      width: 5000px;
      height: 100%;
      background-color: #fff;
      z-index: 2;
    }
  }
  &__desc{
    @include grid100gap(12px);
    margin-top: 24px;
    color: $gray;
    max-width: 401px;
    p{
      line-height: inherit;
    }
  }
  &__hours{
    position: relative;
    margin-top: 47px;
    &-bg{
      position: absolute;
      height: 187px;
      width: 3000px;
      background-color: $graySuperLight;
      right: 40px;
      top: 50%;
      transform: translateY(-50%);
      &:before,
      &:after{
        content: '';
        position: absolute;
        top: 0;
        width: 40px;
        height: 100%;
        background-color: inherit;
      }
      &:before{
        left: 100%;
        clip-path: polygon(0% 0,0 100%,100% 50%);
        transform: translateX(-1px);
      }
      &:after{
        right: 100%;
        clip-path: polygon(100% 0,0 50%,100% 100%);
        transform: translateX(1px);
      }
    }
    &-img{
      width: 359px;
      display: block;
      position: relative;
      z-index: 2;
    }
  }
  &__slider{
    .swiper{
      &-container{
        width: 372px;
        height: 773px;
        position: relative;
      }
      &-slide{
        position: static;
        .chat {
          height: 565px;
          width: 272px;
        }
        &-active{
          .chat{
            width: 100%;
            height: 773px;
            &__text{
              opacity: 1;
              transition-delay: .3s;
            }
          }
        }
        &-next{
          &+.swiper-slide{
            .chat{
              transform: translateX(-100px);
            }
            &+.swiper-slide{
              .chat{
                transform: translateX(-200px);
              }
              &+.swiper-slide{
                .chat{
                  transform: translateX(-300px);
                }
                &+.swiper-slide{
                  .chat{
                    transform: translateX(-400px);
                  }
                  &+.swiper-slide{
                    .chat{
                      transform: translateX(-500px);
                    }
                    &+.swiper-slide{
                      .chat{
                        transform: translateX(-600px);
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      &-pagination{
        position: static;
        width: unset;
        font-weight: 700;
        font-size: 28px;
        line-height: 28px;
        color: $green;
        letter-spacing: -3px;
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
        &-total{
          font-size: 16px;
        }
      }
      &-button{
        position: relative;
        left: unset;
        right: unset;
        margin: 0 0 0 11px;
        transform: unset;
        width: 58px;
        padding: 0;
        &:before{
          width: 10px;
        }
        &:hover{
          transform: scale(1.03);
        }
      }
    }
  }
  &__nav{
    position: absolute;
    bottom: 16px;
    left: 344px;
    width: 356px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
}

.chat{
  transition: all .3s;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__text{
    position: absolute;
    left: 354px;
    bottom: 98px;
    opacity: 0;
    transition: all .3s;
    padding: 14px 28px 14px 8px;
    background-color: #fff;
    width: 336px;
    text-align: right;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      width: 20px;
      height: 100%;
      background-color: inherit;
      right: 100%;
      clip-path: polygon(100% 0,0 50%,100% 100%);
      transform: translateX(1px);
    }
  }
}

.qa{
  padding: 48px 0;
  &__title{
    text-align: center;
  }
  &__items{
    margin-top: 36px;
  }
}

.blog{
  margin: 48px 0;
  overflow: hidden;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: calc(67% - 15px) calc(33% + 15px);
  }
  &__nav{
    margin-top: auto;
    padding-top: 36px;
    display: flex;
  }
  &__decor{
    position: absolute;
    max-width: 100%;
    z-index: 2;
  }
  &__decor1{
    left: 0;
    bottom: 0;
  }
  &__decor2{
    top: 0;
    right: 0;
  }
  &__decor3{
    display: none;
    right: 0;
    bottom: 0;
  }
}

.news,
.events{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    width: 3000px;
    height: 100%;
    pointer-events: none;
  }
  &__inner{
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  &__grid{
    margin-top: 36px;
  }
}

.news{
  padding: 56px 0;
  color: #fff;
  &:before{
    background-color: $green;
    right: 0;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 15px));
    gap: 30px;
  }

}

.events{
  padding: 56px 0 56px 30px;
  &:before{
    background-color: $graySuperLight;
    left: 0;
  }
}

.routes{
  margin: 48px 0;
  position: relative;
  &__title{
    position: absolute;
    top: 56px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
    width: 100%;
    padding: 0 14px;
    pointer-events: none;
    text-align: center;
  }
}

.map{
  height: 700px;
  .ymaps-2-1-79-copyright,
  .ymaps-2-1-79-map-copyrights-promo{
    opacity: 0;
  }
  .balloon__icon{
    background-color: $primary;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: $green;
  }
  .ymaps-2-1-79-controls__control{
    top: 290px !important;
    right: 25px !important;
    left: unset !important;
    height: auto !important;
  }
  .ymaps-2-1-79-zoom{
    height: 92px !important;
    &__scale{
      display: none;
    }
    &__minus,
    &__plus{
      background-size: 100% auto;
      background-repeat: no-repeat;
      width: 42px;
      height: 44px;
      padding: 0;
      background-color: transparent !important;
      box-shadow: none;
      @include center;
      position: static !important;
      transition: all .3s;
      .ymaps-2-1-79-zoom__icon{
        margin: auto;
        width: 12px;
        height: 12px;
        background-size: 100% auto;
        background-repeat: no-repeat;
        position: static;
        border: none;
      }
      &:hover{
        opacity: .8;
      }
    }
    &__minus{
      background-image: url(../img/btn-rect.svg);
      .ymaps-2-1-79-zoom__icon{
        background-image: url(../img/minus.svg);
      }
    }
    &__plus{
      background-image: url(../img/btn-rect2.svg);
      .ymaps-2-1-79-zoom__icon{
        background-image: url(../img/plus.svg);
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .main{
    &__round1{
      left: -25px;
    }
    &__round2{
      right: -264px;
    }
  }

  .develop{
    &__left{
      padding: 56px;
    }
  }

  .how-work{
    &__grid{
      margin-top: 32px;
    }
  }

  .blog{
    &__decor{
      display: none;
    }
  }
}

@media screen and (max-width: 1300px){
  .service__nav,
  .chat__text{
    width: 307px;
    padding-right: 0;
    left: 344px;
  }
}


@media screen and (max-width: 1200px){
  .advantages{
    &__grid{
      grid-template-columns: repeat(1,100%);
    }
  }

  .help{
    grid-template-columns: 100%;
    &__col{
      padding: 64px 40px;
    }
    &__bottom{
      padding-top: 104px;
    }
  }

  .tariffs{
    &__grid{
      grid-template-columns: repeat(3,calc((100% / 3) - (32px / 3)));
      gap: 16px;
    }
  }

  .how-work{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 8px));
      gap: 16px;
    }
  }

  .service{
    &__inner{
      display: block;
    }
    &__left{
      width: 100%;
      margin: 0 0 40px;
      display: flex;
      justify-content: space-between;
    }
    &__hours{
      width: 284px;
      flex-shrink: 0;
      margin-left: 76px;
      margin-top: 0;
      &-img{
        width: 100%;
      }
      &-bg{
        right: unset;
        left: -60px;
      }
    }
  }
}


@media screen and (max-width: 1100px){
  .blog{
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__decor3{
      display: block;
    }
  }

  .news,
  .events{
    padding: 56px 32px;
    &:before{
      display: none;
    }
    &__grid{
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__inner{
      position: static;
    }
  }

  .news{
    background-color: $green;
  }

  .events{
    background-color: $graySuperLight;
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding: 60px 0 124px;
    }
    &__title{
      max-width: 570px;
    }
    &__info{
      width: 335px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 16px;
    }
    &__group{
      margin: 0 !important;
    }
    &__only{
      grid-column: 1/-1;
      width: 100%;
    }
  }

  .inspect{
    padding-top: 8px;
    margin-top: -32px;
    &__inner{
      padding-right: 36px;
    }
    &__media{
      width: 288px;
    }
    &__img{
      width: 233px;
      top: -8px;
      right: -19px;
      transform: unset;
    }
  }

  .advantages{
    margin-top: 16px;
    &__grid{
      gap: 16px;
    }
  }

  .review{
    margin-top: 40px;
    padding: 40px 0 57px;
    &__inner{
      padding: 32px 32px 147px;
    }
    &__info{
      width: 585px;
      gap: 12px;
    }
  }

  .video-btn{
    width: 88px;
    height: 88px;
    margin-top: 19px;
    img{
      width: 20px;
    }
  }

  .help{
    padding: 57px 0 40px;
  }

  .develop{
    padding: 40px 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 18px;
    }
    &__left{
      width: 100%;
      &:before{
        transform: translateY(-1px);
        top: 100%;
        left: 0;
        width: 100%;
        height: 18px;
        clip-path: polygon(0 0,50% 100%,100% 0%);
      }
    }
    &__title{
      max-width: 470px;
    }
    &__desc{
      max-width: 100%;
    }
    &__right{
      padding: 37px 37px 56px;
    }
  }

  .tariffs,
  .how-work,
  .service,
  .qa,
  .blog{
    padding: 40px 0;
  }

  .blog{
    margin: 0;
  }

  .routes{
    margin: 40px 0;
  }

  .map{
    .ymaps-2-1-79-controls__control{
      top: 600px !important;
      width: 100% !important;
      right: 0 !important;
    }
    .ymaps-2-1-79-zoom{
      width: 100% !important;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: row-reverse;
      &__plus,
      &__minus{
        margin: 0 2px;
        transform: rotate(90deg);
      }
      &__icon{
        transform: rotate(-90deg);
      }
    }
  }
}


@media screen and (max-width: 600px){
  .main{
    &__inner{
      padding: 44px 0 114px;
    }
    &__info{
      width: 265px;
    }
    &__group{
      &-num{
        font-size: 36px;
      }
    }
    &__only{
      padding: 8px 30px 8px 16px;
    }
  }

  .inspect{
    margin-top: -16px;
    padding: 0;
    overflow: unset;
    &__inner{
      padding: 20px;
    }
    &__desc{
      max-width: 180px;
    }
    &__media{
      width: 167px;
    }
    &__img{
      width: 116px;
      top: 50px;
      right: -14px;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 8px;
    }
    &__triangle{
      &-desktop{
        display: none;
      }
      &-mob{
        display: block;
      }
    }
  }

  .review{
    margin-top: 28px;
    padding: 28px 0 77px;
    &__inner{
      padding: 20px 20px 122px;
    }
    &__info{
      gap: 4px;
    }
  }

  .help{
    padding: 36px 0 28px;
    &__col{
      padding: 28px 20px;
    }
    &__top{
      gap: 4px;
    }
    &__bottom{
      padding-top: 80px;
    }
    &__btn{
      width: unset;
    }
    &__video{
      max-width: 230px;
    }
  }

  .develop{
    padding: 28px 0;
    &__left,
    &__right{
      padding: 20px;
    }
    &__title{
      max-width: 190px;
    }
    &__desc{
      margin-top: 8px;
      max-width: 250px;
    }
    &__decor{
      &-desktop{
        display: none;
      }
      &-mob{
        display: none;
      }
    }
  }

  .tariffs{
    padding: 28px 0;
    &__badge{
      max-width: 260px;
      margin-top: 8px;
    }
    &__grid{
      display: flex;
      gap: unset;
      width: calc(100% + 28px);
      margin-left: -14px;
      padding: 0 14px 16px;
      margin-top: 24px;
      overflow-x: auto;
      @include scroll;
    }
  }

  .how-work{
    padding: 28px 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 2px));
      gap: 4px;
      margin-top: 24px;
    }
  }

  .service{
    padding: 28px 0;
    &__left{
      display: block;
    }
    &__top{
      text-align: center;
    }
    &__title{
      br{
        display: none;
      }
    }
    &__desc{
      margin-top: 16px;
    }
    &__hours{
      width: 100%;
      margin: 24px 0 0;
      padding: 0 24px;
      &-bg{
        left: 40px;
      }
    }
    &__slider{
      padding-bottom: 57px;
      position: relative;
      .swiper{
        &-container{
          width: 276px;
          height: 573px;
          position: static;
        }
        &-slide{
          .chat{
            height: 457px;
            width: 220px;
          }
          &-active{
            .chat{
              width: 100%;
              height: 573px;
            }
          }
          &-next{
            &+.swiper-slide{
              .chat{
                transform: translateX(-56px);
              }
              &+.swiper-slide{
                .chat{
                  transform: translateX(-112px);
                }
                &+.swiper-slide{
                  .chat{
                    transform: translateX(-168px);
                  }
                }
              }
            }
          }
        }
        &-pagination{
          display: none;
        }
        &-button{
          height: 44px;
          width: 37px;
          margin-left: 4px;
          &:before{
            width: 5px;
          }
          img{
            width: 14px;
          }
        }
      }
    }
    &__nav{
      //position: static;
      width: unset;
    }
    &__buttons{
      transform: translateY(-50%);
      position: absolute;
      top: 100%;
      right: 35px;
      //margin-top: 13px;
      z-index: 101;
    }
  }

  .chat{
    &__text{
      left: 0;
      top: 100%;
      background-color: transparent;
      padding: 0;
      text-align: left;
      margin-top: 12px;
      max-width: 220px;
      font-size: 16px;
      &:before{
        display: none;
      }
    }
  }

  .qa{
    padding: 28px 0;
    &__items{
      margin-top: 24px;
    }
  }

  .blog{
    padding: 28px 0;
    &__nav{
      padding-top: 20px;
    }
  }

  .events,
  .news{
    padding: 32px 14px;
    &__grid{
      gap: 32px;
      margin-top: 20px;
    }
  }

  .routes{
    margin: 28px 0;
    &__title{
      top: 20px;
      max-width: 250px;
    }
  }

  .map{
    height: 480px;
    .ymaps-2-1-79-controls__control{
      top: 380px !important;
      width: 100% !important;
      right: 0 !important;
    }
  }
}
